import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, from } from 'rxjs';

import { Statement } from '@core/models/statement/statement';
import { environment as ENV } from '@environment';
import { Pdfdata } from '@app/core/models/statement/pdfdata';

@Injectable()
export class StatementsService {

  constructor(private http: HttpClient) { }

  public get_periods(account: string, type: string): Observable<Statement>  {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const user = {
      id: '33210989',
      idType: 'CC',
      accountId: account,
      accountType: type.toUpperCase(),
      companyId: 'BANCO_POPULAR',
      ipAddress: '192.168.0.1',
      requestId: new Date().getTime()
    };

    // return this.http.get<Statement>(ENV.api.services.statements);
    return this.http.post<Statement>(ENV.api.base + ENV.api.services.statements, user, httpOptions);
  }

  public get_pdf(account: String): Observable<Pdfdata>  {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const user = {
      id: '19800203',
      idType: 'CC',
      accountId: account,
      accountType: 'CREDIT_CARD',
      companyId: 'BANCO_POPULAR',
      ipAddress: '192.168.0.1',
      requestId: '1288883999'
    };

    return this.http.get<Pdfdata>(ENV.api.services.pdf);
    // return this.http.post<Statement>(ENV.api.base + ENV.api.services.statements, user, httpOptions);
  }
}
