import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Statement } from '@core/models/statement/statement';
import { StatementsService } from '@app/core/services/statements/statements.service';
import { Pdfdata } from '@app/core/models/statement/pdfdata';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-statements',
  templateUrl: './statements.container.html',
  styleUrls: ['./statements.container.sass'],
  encapsulation: ViewEncapsulation.None
})
export class StatementsContainer implements OnInit {

  private statement: Statement;
  private pdfData: Pdfdata;
  public statementGenForm: FormGroup;
  public inProcess: Boolean = false;
  public isReadyToDownload: Boolean = false;
  public parameters: any;

  constructor(
    private http: StatementsService,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute) { 
  }

  ngOnInit(): void {
    this.statementGenForm = this.formBuilder.group({
      period: ['', [Validators.required]]
    });

    this.route.paramMap.subscribe(params => {
      if (!isNullOrUndefined(params.get('type') && params.get('id'))) {
        this.parameters = params;
      }
    }
    ).unsubscribe();

    this.doPopulatePeriods();
  }

  doPopulatePeriods(): void {
    this.http.get_periods(this.parameters.params.id, this.parameters.params.type).subscribe(data => {
      this.statement = data;
    });
  }

  doGenerate(): void {
    this.inProcess = true;
    this.http.get_pdf(this.parameters.params.id).subscribe(data => {
      this.pdfData = data;
      this.isReadyToDownload = true;
    });
  }

  get hasPeriodsAvailables(): boolean {
    return !isNullOrUndefined(this.statement);
  }

}
